import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Header from "../components/Blog/Header";
import Contact from "../components/Contact";
import Line from "../components/Home/Line";
import Loader from "../components/Loader";
import AnimatedPage from "../Utils/AnimatedPage";

const About = () => {
  const [loader, setloader] = useState(true);

  function closeLoader(params) {
    setloader(false);
  }

  useEffect(() => {
    setTimeout(() => {
      closeLoader();
    }, 2000);
  });

  const url = window.location.href;

  return (
    <>
      {loader ? (
        <Loader />
      ) : (
        <AnimatedPage>
          <Header />
          <div className="detail">
            <div className="detailImg">
              <img src="/about/b.jpg" alt="author" />
            </div>

            <div className="detailPiece">
              <p>
                My name is yahaya waliyah jumai. the founder of 'A CUP OF ME'
                and basically, i write .writting became a passion when i found
                it hard to express myself in public and i realized i have one of
                nature's greatest gift which is putting putting my word in ink
                and let it speak for me .i am also a biomedical engineering
                student of university of ilorin . having personal blogs is one
                of my most procrastinated dream and i am excited about seeing
                this dream through as you have a cup of my thought full to the
                brim.
              </p>
            </div>
            <Link to="/blog">
              <div className="detailGoback">
                <div
                  style={{
                    color: "white",
                    background: "red",
                    padding: "5px 10px",
                    fontWeight: "500",
                    margin: "10px 0px",
                  }}
                >
                  Go back
                </div>
              </div>
            </Link>
          </div>
          <Line />
          <Contact />
        </AnimatedPage>
      )}
    </>
  );
};

export default About;
